import React, { useState } from "react";
import FirstPage from "./Main/FirstPage/FirstPage";
import style from "./main.module.scss"
import SignIn from "./Main/SignIn-Up/SignIn/SignIn";
import { Routes, Route} from "react-router-dom";
import SignUp from "./Main/SignIn-Up/SignUp/SignUp";
import { UserHomePage } from "./UserHomePage/UserHomePage";

const Main = () => {
    const [user, setUser] = useState({});
    const handleUser = (user) => {
        setUser(user)
    }
    return (
    <div className={style.wrapper_body}>
        <div className={style.container}>
            <Routes>
            <Route path="/*" element={<FirstPage />}  />
            <Route path="/login" element={<SignIn handleUser={handleUser} />}  />
            <Route path="/SignUp" element={<SignUp handleUser={handleUser} />} />
            <Route path="/User" element={<UserHomePage user={user} />} />
            <Route path="*" element={<FirstPage />}  />
            </Routes>
        </div>
    </div>
    )
}
export default Main;

